<script>
import qs from "qs";
import Footer from "../components/common/Footer.vue";
import RelationI from "../components/common/RelationI.vue";
import Service from "../components/common/Service.vue";
import Nav from "../components/Nav.vue";
export default {
  data() {
    // <!--验证手机号是否合法-->
    let checkTel = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入手机号码"));
      } else if (!this.checkMobile(value)) {
        callback(new Error("手机号码不合法"));
      } else {
        callback();
      }
    };
    //  <!--验证码是否为空-->
    let checkSmscode = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入手机验证码"));
      } else {
        callback();
      }
    };
    let checkEmail = (rule, value, callback) => {
      const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
      if (!value) {
        return callback(new Error("邮箱不能为空"));
      }
      setTimeout(() => {
        if (mailReg.test(value)) {
          callback();
        } else {
          callback(new Error("请输入正确的邮箱格式"));
        }
      }, 100);
    };

    return {
      centerDialogVisible: false,
      ruleForm2: {
        name: "",
        firm: "",
        email: "",
        phone: "",
        code: "",
      },
      checked: true,
      //  表单验证
      rules2: {
        name: [
          { required: true, message: "请输入姓名", trigger: "change" },
          { min: 2, max: 6, message: "长度在 2到 6 个字符", trigger: "change" },
        ],
        firm: [
          { required: true, message: "请输入企业名称", trigger: "change" },
          {
            min: 2,
            max: 30,
            message: "长度在 2到 30 个字符",
            trigger: "change",
          },
        ],
        email: [{ validator: checkEmail, trigger: "change" }],
        phone: [{ validator: checkTel, trigger: "change" }],
        code: [{ validator: checkSmscode, trigger: "change" }],
      },
      buttonText: "发送验证码",
      isDisabled: false, // 是否禁止点击发送验证码按钮
      flag: true,
    };
  },
  components: { Nav, Footer, RelationI, Service },
  methods: {
    // <!--发送验证码-->
    sendCode() {
      console.log(this.checked);
      let phone = this.ruleForm2.phone;
      console.log(phone);
      this.$axios
        .post(
          "http://47.243.109.87:8082/caiweiGw/subscribe/code",
          qs.stringify({
            phone: phone,
          })
        )
        .then((res) => {
          if (res.status != 200) {
            return this.$message.error("验证码发送失败");
          } else {
            if (this.checkMobile(phone)) {
              // console.log(tel);
              let time = 60;
              this.buttonText = "已发送";
              this.isDisabled = true;
              if (this.flag) {
                this.flag = false;
                let timer = setInterval(() => {
                  time--;
                  this.buttonText = time + " 秒";
                  if (time === 0) {
                    clearInterval(timer);
                    this.buttonText = "重新获取";
                    this.isDisabled = false;
                    this.flag = true;
                  }
                }, 1000);
              }
            }
          }
        });
    },
    // 验证手机号
    checkMobile(str) {
      let re = /^1\d{10}$/;
      if (re.test(str)) {
        return true;
      } else {
        return false;
      }
    },
    onSubmit(ruleForm2) {
      // console.log({ ...this.ruleForm2 });
      // let param = new URLSearchParams()
      // this.$axios.post("http://47.243.109.87:8082/caiweiGw/subscribe/add").then(res=>{
      //   console.log(res);
      // }).catch(err=>{
      //   console.log(err);
      // })
      if (this.checked === true) {
        let obj = { ...this.ruleForm2 };
        this.$axios({
          headers: {
            deviceCode: "A95ZEF1-47B5-AC90BF3",
          },
          method: "post",
          url: "http://47.243.109.87:8082/caiweiGw/subscribe/add",
          data: qs.stringify(obj),
        })
          .then((res) => {
            console.log(res);
            if (res.data.message === 201) {
              this.$message.error("验证码输入不正确");
            } else if (res.data.message == 202) {
              this.$message.error("预约提交失败");
            } else if (res.data.message == 200) {
              this.$message.error("预约已存在");
              this.closeDialog();
            } else if (
              this.ruleForm2.name == "" ||
              this.ruleForm2.firm == "" ||
              this.ruleForm2.email == "" ||
              this.ruleForm2.phone == "" ||
              this.ruleForm2.code == ""
            ) {
              this.$message.error("请填写内容");
            } else {
              this.$message.success("提交成功");
              this.closeDialog();
            }
          })
          .catch((err) => {
            console.log(err);
          });
        console.log("submit!");
      } else {
        this.$message.error("请勾选《用户服务协议》");
      }
    },
    // 关闭弹框
    closeDialog() {
      this.$refs.ruleForm2.resetFields();
      this.centerDialogVisible = false;
    },
  },
};
</script>

